/* Cards */

.title {
    font-family: "Sedan SC", serif;
    font-weight: bolder;
    margin-top: 20px;
}

.card {
    border: none;
    transition: transform 0.4s ease;
    margin-top: 20px;
    height: 550px;
}

.card-img-top {
    height: 190px;
}

.card:hover {
    transform: scale(1.03);
    border: 0.5px solid lightgray;
}

.red-message {
    left: 85%;
    padding: 5px;
    font-size: "14px";
    z-index: 1;
}

.card-title {
    font-family: "Francois One", sans-serif;
    font-weight: bolder;
    letter-spacing: 1px;
    font-style: normal;
}

.card-body {
    height: auto;
    padding: 20px;
    margin-bottom: 20px;
}

.buttons {
    padding-bottom: 20px;
}

@media (min-width: 300px) and (max-width:600px) {
    .card-group {
        padding: 0;
        padding-right: 10%;
    }

    .card {
        border: 0.5px solid black;
        height: 560px;
        margin-top: 2px;
    }

    .red-message {
        left: 20%;
    }
}

/* Navbar */

.logo {
    height: 40px;
    border-radius: 50%;
    margin-left: 20px;
}

.navbar-brand {
    font-family: "Bona Nova SC", serif;
    font-weight: 700;
    font-style: normal;
}

.dark-mode {
    font-size: 30px;
    color: black;
    position: absolute;
    right: 0;
    top: 0;
    margin-top: 7px;
    margin-right: 30px;
}

.dark-mode:hover {
    color: black;
}

/* Spinner */

.spinner-border {
    border: 5px solid rgba(0, 0, 0, 0.1);
    border-top-color: #0078D4;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}


/* Hover Arrow */

.hover-arrow {
    position: relative;
    padding-right: 30px;
}

.hover-arrow a {
    text-decoration: none;
}

.hover-arrow::after {
    content: 'â†’';
    font-size: 29px;
    position: absolute;
    top: 30%;
    left: 0%;
    transform: translateY(-50%);
    opacity: 0;
    transition: opacity 0.3s ease, left 0.9s ease;
}

.hover-arrow:hover::after {
    left: calc(35% + 10px);
    opacity: 1;
}